import React from 'react';
import navbarHorizontalLine from '../images/line-6.svg';
import redNavbarHorizontalLine from '../images/line-10.svg';
import './stylesheet.css';

export type SectionProps = {
  title: string;
  isActive: boolean;
  isHover: boolean;
  onMouseEnter: () => void;
  onMouseLeave: () => void;
  onClick: () => void;
}

function getClassName(isActive: boolean, isHover: boolean): string {
  if (isActive) {
    return "underline red navbar-header"
  } else if (isHover) {
    return "red navbar-header"
  } else {
    return "navbar-header"
  }
}

const NavbarSection = ({title, isActive, isHover, onMouseEnter, onMouseLeave, onClick}: SectionProps) => {
  const className = getClassName(isActive, isHover);
  return (<div onMouseEnter={onMouseEnter} onMouseLeave={onMouseLeave} onClick={onClick}
               className={title.toLowerCase() + "-navbar-section"}>
    <img src={isActive || isHover ? redNavbarHorizontalLine : navbarHorizontalLine} alt="line"
         className={title.toLowerCase() + "-line"}
    />
    <div className={className + " " + title.toLowerCase()}>
      {title}
    </div>
  </div>);
}

export default NavbarSection;