import React from 'react';
import allGraphic from '../images/apcha-all.svg';
import workGraphic from '../images/work-apcha.svg';
import projectsGraphic from '../images/projects-apcha.svg';
import contactGraphic from '../images/contact-apcha.svg';
import publicationsGraphic from '../images/publications-apcha.svg';
import {Section} from "./navbar";
import './stylesheet.css';

type Props = {
  section: Section
}

function getGraphicUrl(section: Section): string {
  switch (section) {
    case Section.About:
      //TODO: update graphic?
      return allGraphic;
    case Section.Publications:
      return publicationsGraphic;
    case Section.Work:
      return workGraphic;
    case Section.Projects:
      return projectsGraphic;
    case Section.Contact:
      return contactGraphic;
  }
  return JSON.stringify(section);
}

//TODO: eventually needs to be updated via scrolling
const NavbarGraphic = ({section}: Props) => {
  return (
    <img src={getGraphicUrl(section)} alt="navbar graphic" className="navbar-graphic"/>
  );
}

export default NavbarGraphic;