import React, {useState} from 'react';
import NavbarSection, {SectionProps} from "./navbar-section";
import SocialIcons from "./social-icons";
import NavbarGraphic from "./navbar-graphic";
import ContentContainer from "./content-container";

export enum Section {About, Publications, Work, Projects, Contact}

export type HoverState = Section | null

export type NavbarState = {
  activeSection: Section;
  hoverState: HoverState;
}

const initialState: NavbarState = {
  activeSection: Section.About,
  hoverState: null
}

function getSectionProps(navbarState: NavbarState,
                         setNavBarState: (n: NavbarState) => void,
                         section: Section): SectionProps {
  return {
    title: Section[section].toUpperCase(),
    isActive: navbarState.activeSection === section,
    isHover: navbarState && navbarState.hoverState === section,
    onMouseEnter: () =>
      setNavBarState({
        ...navbarState,
        hoverState: section,
      })
    ,
    onMouseLeave: () =>
      setNavBarState({
        ...navbarState,
        hoverState: navbarState.hoverState === section ? null : navbarState.hoverState,
      }),
    onClick: () =>
      setNavBarState({
        ...navbarState,
        activeSection: section,
      })
  }
}

function Navbar() {
  const [navBarState, setNavBarState] = useState<NavbarState>(initialState)
  return (
    <div>
      <div className="navbar">
        <div className="name poppins-normal-black-24px">Alexander Piatko Chang</div>
        <div className="software-engineer-stripe poppins-normal-black-24px">software engineer @ stripe</div>
        <div className="navbar-sections-container">
          <NavbarSection {...getSectionProps(navBarState, setNavBarState, Section.About)}/>
          <NavbarSection {...getSectionProps(navBarState, setNavBarState, Section.Publications)}/>
          <NavbarSection {...getSectionProps(navBarState, setNavBarState, Section.Work)}/>
          <NavbarSection {...getSectionProps(navBarState, setNavBarState, Section.Projects)}/>
          <NavbarSection {...getSectionProps(navBarState, setNavBarState, Section.Contact)}/>
          <NavbarGraphic
            section={navBarState.hoverState != null ? navBarState.hoverState : navBarState.activeSection}/>
        </div>
        <SocialIcons/>
      </div>
      <ContentContainer section={navBarState.activeSection}/>
    </div>
  )
}

export default Navbar