import React from 'react';
import {Section} from "./navbar";
import About from "./about";
import Publications from "./publications";
import Projects from "./projects";
import Contact from "./contact";
import Work from "./work";

type Props = {
  section: Section
}

function getContent(section: Section): React.ReactNode {
  switch (section) {
    case Section.About:
      return <About/>;
    case Section.Publications:
      return <Publications/>;
    case Section.Work:
      return <Work/>;
    case Section.Projects:
      return <Projects/>;
    case Section.Contact:
      return <Contact/>;
  }
}

//TODO: styling/hiding
const ContentContainer = ({section}: Props) => {
  return (<div>
    {getContent(section)}
  </div>)
}

export default ContentContainer;